nav {
  width: 100%;
  font-size: 2rem;
  color: #fff;
  margin-bottom: 50px;
  background-color: green;
  height: auto;
}

a {
  color: #fed420;
}

a:hover {
  color: #fed420;
  text-decoration: none;
}

nav li {
  display: inline-block;
  padding: 26px 0;
  width: 35%;
  /* text-align: center; */
}

ul {
  /* padding-left: 0px; */
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}

@media only screen and (max-width: 750px) {
  #cur-sco,
  #top-sco {
    font-size: 2rem;
  }
}



